<template>
  <group class="jq-mt">
  <popup-picker :title="dateTile" :data="timeList" :value.sync="time" ></popup-picker>
   </group>
  <flexbox class="topH">
    <flexbox-item @click='clickSalesMap'><div class="butTIt">销售收入：{{salas}}亿</div></flexbox-item>
    <flexbox-item @click='ckickCosts'><div class="butTIt">成本支出：{{conts}}亿</div></flexbox-item>
  </flexbox>
  <group  @click='ckickProfits' class="jq-mt">
    <div class="chart" v-ebar="barLineData"></div>
  </group>
  <flexbox class="jq-mt">
    <!--销售仪表盘-->
    <flexbox-item>
      <group @click='clickSales'>
        <div class="panLittle" v-egauge="gaugeSalesData"></div>
      </group>
    </flexbox-item>
  </flexbox>
  <flexbox class="jq-mt">
    <flexbox-item>
      <!--成本仪表盘-->
      <group @click='ckickCosts'>
      <div class="panLittle" v-egauge="gaugeContData"></div>
      </group>
    </flexbox-item>
    <flexbox-item>
      <!--利润仪表盘-->
      <group @click='ckickProfits'>
      <div class="panLittle" v-egauge="gaugeProfitData"></div>
      </group>
    </flexbox-item>
  </flexbox>
</template>
<script>
  //import { Group,FlexboxItem, Flexbox,PopupPicker } from 'vux/src/components'
  import {Flexbox,FlexboxItem} from '*/flexbox'
  import PopupPicker from '*/popup-picker'
  import Group from '*/group'
  import { getList } from '../../../untils/api'

  export default{
    data () {
      return {
        date: new Date(),
        msg: ' 财务状况 ',
        title: ' 年财务状况',
        value: '',
        salas:'',
        conts:'',
        dateTile:'日期',
        time: formatData.defaultTime,
        timeList: formatData.dateList,
        barLineData:{},
        gaugeSalesData:{},
        gaugeContData:{},
        gaugeProfitData:{}
      }
    },
    components: {
      Group,
      PopupPicker,
      FlexboxItem,
      Flexbox
    },
    ready () {
     this.getDataOne();
     document.title = '销售管理';
    },
      methods: {
         clickSalesMap(){
           this.$route.router.go({name:'salesmap'})
        },
        clickSales(){
           this.$route.router.go({name:'saleslive'})
        },
        ckickCosts(){
          this.$route.router.go({name:'cb'})
        },
        ckickProfits(){
          this.$route.router.go({name:'lr'})
        },
        async getBarLineData() {
            commit('UPDATE_LOADING', true)
          let id = "APP10118"
          let date=formatData.dateConversion(this.time);
          let dataTit = await getList(id,{date_:date});
          if(dataTit.success){
            const TableList = dataTit.data;
            this.barLineData = {};
            this.barLineData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
              commit('UPDATE_LOADING', false)
          }else{
              commit('UPDATE_LOADING', false)
            console.log(dataTit.message);
          }
          let barLineData = this.barLineData
        },
        async getDataOne() {
            commit('UPDATE_LOADING', true)
            let id = "APP10130"
            let date=formatData.dateConversion(this.time);
            let dataTit = await getList(id,{date_:date});
              if(dataTit.success){
               this.salas=dataTit.data.result.list[0].SALE_;
               this.conts=dataTit.data.result.list[0].COST_;
                 commit('UPDATE_LOADING', false)
              }else{
              commit('UPDATE_LOADING', false)
            console.log(dataTit.message);
          }
        },
        async GetSalesData() {
            commit('UPDATE_LOADING', true)
            let id = "APP10131"
            let date=formatData.dateConversion(this.time);
            let dataTit = await getList(id,{date_:date});
            //console.log(dataTit);
          if(dataTit.success){
            const TableList = dataTit.data;
            this.gaugeSalesData = {};
            this.gaugeSalesData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
              commit('UPDATE_LOADING', false)
          }else{
              commit('UPDATE_LOADING', false)
            console.log(dataTit.message);
          }
          let gaugeSalesData = this.gaugeSalesData
        },
        async GetContData() {
            commit('UPDATE_LOADING', true)
            let id = "APP10132"
            let date=formatData.dateConversion(this.time);
            let dataTit = await getList(id,{date_:date});
            //console.log(dataTit);
          if(dataTit.success){
            const TableList = dataTit.data;
            this.gaugeContData = {};
            this.gaugeContData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
              commit('UPDATE_LOADING', false)
          }else{
              commit('UPDATE_LOADING', false)
            console.log(dataTit.message);
          }
          let gaugeContData = this.gaugeContData
        },
        async GetProfitData() {
            commit('UPDATE_LOADING', true)
            let id = "APP10133"
            let date=formatData.dateConversion(this.time);
            let dataTit = await getList(id,{date_:date});
            //console.log(dataTit);
          if(dataTit.success){
            const TableList = dataTit.data;
            this.gaugeProfitData = {};
            this.gaugeProfitData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
              commit('UPDATE_LOADING', false)
          }else{
              commit('UPDATE_LOADING', false)
            console.log(dataTit.message);
          }
          let gaugeProfitData = this.gaugeProfitData
        }
    },
    watch: {
     time (newtime, oldtime) {
      this.getBarLineData();
      this.GetSalesData();
      this.GetContData();
      this.GetProfitData();
      this.getDataOne();
       formatData.setDateTime(newtime);
     }
    },
    created: function () {
      this.barLineData = {};
      this.barLineData.dataset = [];
      this.getBarLineData();

      this.gaugeSalesData = {};
      this.gaugeSalesData.dataset = [];
      this.GetSalesData();

      this.gaugeContData = {};
      this.gaugeContData.dataset = [];
      this.GetContData();

      this.gaugeProfitData = {};
      this.gaugeProfitData.dataset = [];
      this.GetProfitData();
    }
  }
</script>



